<template>
    <div>
        <div v-for="(value,index) in theMsg" v-bind:key="index">
            <div class="msg">{{ value }}</div>
            <button class="btn btn1" @click="delMsg(index)">删除</button>
        </div>
    </div>
</template>

<script>
export default{
    name:"Part",
    props:['theMsg'],
    methods: {
        delMsg:function(index){
            this.$emit('del',index)
        }
    },
}
</script>

<style>
.msg{
    outline-style: none ;
    border: 1px solid #ccc; 
    border-radius: 3px;
    padding: 6px;
    width: 300px;
    font-size: 14px;
    font-family: "Microsoft soft";  
    display: inline-block;
}
button {
      /* 文字颜色 */
      color: #0099CC; 
      /* 清除背景色 */
      background: transparent; 
      /* 边框样式、颜色、宽度 */
      border: 2px solid #0099CC;
      /* 给边框添加圆角 */
      border-radius: 6px; 
      /* 字母转大写 */
      border: none;
      color: white;
      padding: 6px 12px;
      text-align: center;
      display: inline-block;
      font-size: 16px;
      margin: 4px 2px;
      -webkit-transition-duration: 0.4s; /* Safari */
      transition-duration: 0.4s;
      cursor: pointer;
      text-decoration: none;
      text-transform: uppercase;
      background-color: white; 
      color: black; 
      border: 2px solid #008CBA;
}
/* 悬停样式 */
button:hover {
      background-color: #008CBA;
      color: white;
}
</style>
