<template>
  <div id="all">
    <div class="fstyle">NOTE</div>
  <div id="In">
  <input type="text" id="theInput" v-model="singleMsg" placeholder="输入待办事项">
  </div>
  <button  @click="add">添加</button>
  <br><br>
  <Part :theMsg="msg" @del="del"/>
  </div>

</template>

<script>
import Part from './components/Part'

export default{
  name:'App',
  components:{Part},
  data(){
    return{
      msg:[],
      singleMsg:''
    }
  },
  methods: {
    add:function(){
      if(this.singleMsg=='')return
      this.msg.push(this.singleMsg)
      this.singleMsg=''
    },
    del:function(index){
      this.msg.splice(index,1)
    }
  },

}
</script>

